import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import {
  CardContent,
  CardActions,
  Collapse,
  Card,
  CardHeader,
  CardMedia,
  Avatar,
  IconButton,
  Typography,
} from "@material-ui/core";
import { red } from "@material-ui/core/colors";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import ModeCommentIcon from "@material-ui/icons/ModeComment";
import Chip from "@material-ui/core/Chip";




const Post = ({ owner, date, description, image, tags, comments }) => {

  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const handleClick = () => {
    console.info("You clicked the Chip.");
  };

  
  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            {owner.charAt(0)}
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            {/* <MoreVertIcon /> */}
          </IconButton>
        }
        title={owner}
        // subheader={date}
      />
      <CardMedia className={classes.media} image={image} title="" />
      <CardContent>
        <div className={classes.chipList}>
          {tags.map((data, index) => {
            return (
              <Chip
                key={index}
                className={classes.chip}
                size="small"
                label={"#" + data}
                onClick={handleClick}
              />
            );
          })}
        </div>
        <Typography variant="body2" color="textSecondary" component="p">
          {description}
        </Typography>
        <Typography variant="caption" color="textSecondary" component="p">
          {date}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ModeCommentIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          {comments.map((data, index) => {
            return <Typography key={index} paragraph>{data}</Typography>;
          })}
        </CardContent>
      </Collapse>
    </Card>
  );
};

const useStyles = makeStyles((theme) => ({
  // root: {
  //   flexGrow: 1,
  //   overflow: "hidden",
  //   padding: theme.spacing(10, 3),
  //   height: "100%",
  // },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
  },
  chip: {
    backgroundColor: "#f7ac97",
    margin: "0.5vh 1vh 0.5vh 0",
  },
  chipList: {
    display: "flex",
    justifyContent: "left",
    flexWrap: "wrap",
    padding: "1vh 2vh 1vh 0",
  },
  root: {},
  media: {
    height: 200,
    paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    // transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  },
}));

export default Post;
