import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";

import {
  InputLabel,
  InputAdornment,
  FormControl,
  OutlinedInput,
  IconButton,
  Button,
} from "@material-ui/core";

import Alert from '@material-ui/lab/Alert';

import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

import PersonIcon from "@material-ui/icons/Person";
import LockIcon from "@material-ui/icons/Lock";
import { auth } from "../../config/firebase";

const Login = ({ onPageChange }) => {
  const classes = useStyles();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showPasswordConfirm, setShowPasswordConfirm] = useState(false);
  const [warningMessage, setWarningMessage] = useState("");

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleClickShowPasswordConfirm = () => {
    setShowPasswordConfirm(!showPasswordConfirm);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handleSubmit = () => {
    if (password === passwordConfirm && username !== "" && password !== "") {
      auth.createUserWithEmailAndPassword(username, password);
      onPageChange("SignIn");
    } else {
      setWarningMessage("As senhas são diferentes.");
    }
  };

  return (
    <div>
      <FormControl fullWidth className={classes.margin} variant="outlined">
        <InputLabel htmlFor="outlined-adornment-amount">e-mail</InputLabel>
        <OutlinedInput
          id="outlined-email"
          value={username}
          onChange={(event) => setUsername(event.target.value)}
          startAdornment={
            <InputAdornment position="start">
              <PersonIcon className={classes.icon} />
            </InputAdornment>
          }
          labelWidth={60}
        />
      </FormControl>
      <FormControl fullWidth className={classes.margin} variant="outlined">
        <InputLabel htmlFor="outlined-adornment-password">senha</InputLabel>
        <OutlinedInput
          id="outlined-password"
          value={password}
          type={showPassword ? "text" : "password"}
          onChange={(e) => setPassword(e.target.value)}
          startAdornment={
            <InputAdornment position="start">
              <LockIcon className={classes.icon} />
            </InputAdornment>
          }
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword}
                onMouseDown={handleMouseDownPassword}
                edge="end"
              >
                {showPassword ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
          labelWidth={70}
        />
      </FormControl>
      <FormControl fullWidth className={classes.margin} variant="outlined">
        <InputLabel htmlFor="outlined-password-confirm">confirmar</InputLabel>
        <OutlinedInput
          id="outlined-adornment-password"
          type={showPasswordConfirm ? "text" : "password"}
          value={passwordConfirm}
          onChange={(e) => setPasswordConfirm(e.target.value)}
          startAdornment={
            <InputAdornment position="start">
              <LockIcon className={classes.icon} />
            </InputAdornment>
          }
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={handleClickShowPasswordConfirm}
                onMouseDown={handleMouseDownPassword}
                edge="end"
              >
                {showPasswordConfirm ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
          labelWidth={70}
        />
      </FormControl>
      <div className={classes.buttonWrapper}>
        <Button
          className={classes.buttonOutlined}
          style={{ marginRight: 7 }}
          variant="outlined"
          onClick={() => onPageChange("SignIn")}
        >
          Voltar
        </Button>
        <Button
          className={classes.buttonOutlined}
          variant="outlined"
          onClick={handleSubmit}
        >
          Cadastrar
        </Button>
      </div>
      { warningMessage !== "" && (<Alert className={classes.alert} variant="outlined" severity="error">
        {warningMessage}
      </Alert>)}
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
  },
  container: {
    textAlign: "center",
  },
  margin: {
    padding: "0px 0px 15px 0px",
  },
  icon: {
    fill: "#666666",
  },
  buttonOutlined: {
    flex: 1,
    fontWeight: "bold",
    color: "#666666",
    margin: "5px 0px 0px 0px",
  },
  buttonBase: {
    margin: "15px 0px 0px 0px",
  },
  buttonWrapper: {
    display: "flex",
  },
  alert: {
    margin: "15px 0px 0px 0px",
  }
}));

export default Login;
