import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Post from "../post/Post";
import { auth, firestore  } from '../../config/firebase';

const mockPostData = [
  {
    owner: "Zezinho",
    date: "September 19, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto1.jpg"),
    tags: ["Projeto1", "Projeto2", "Projeto3", "Projeto4"],
    comments: ["comentario1", "comentario2", "comentario3", "comentario4", "comentario5"]
  },
  {
    owner: "Selma",
    date: "September 1, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto3.jpg"),
    tags: ["Projeto1", "Projeto2"],
    comments: ["comentario1", "comentario2", "comentario3", "comentario4", "comentario5"]
  },
  {
    owner: "Zezinho",
    date: "September 19, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto1.jpg"),
    tags: ["Projeto1", "Projeto2"],
    comments: ["comentario1", "comentario2", "comentario3", "comentario4", "comentario5"]
  },
  {
    owner: "Selma",
    date: "September 1, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto3.jpg"),
    tags: ["Projeto1", "Projeto2"],
    comments: ["comentario1", "comentario2", "comentario3", "comentario4", "comentario5"]
  },
];

const Home = () => {
  const { uid, photoURL } = auth.currentUser;

   const [posts, setPosts] = useState([]);
  
  useEffect(() => {
    firestore.collection('post').where("uid", "==", uid)
      .get().then((snapshot) => {
      if(snapshot){
        let data = [];
        snapshot.forEach((item) => data.push({...item.data() }));
        setPosts(data);
      }
    })
  }, [uid]);

  const classes = useStyles();
  return (
    <div className={classes.root}>
      {/* <Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" /> */}
      <Paper className={classes.paper}>
        {posts.map((post, index) => {
          return (
            <div key={index} className={classes.postItem}>
              <Post 
                owner={mockPostData[0].owner}
                date={post.date}
                description={post.description}
                image={mockPostData[0].image}
                tags={post.projects}
                comments={mockPostData[0].comments}
               />
            </div>
          );
        })}
      </Paper>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    padding: "6vh 1vh",
    height: "100%",
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
  },
  postItem: {
    marginBottom: "20px",
  },
}));

export default Home;
