import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";

import {
  Typography,
  ButtonBase,
  InputLabel,
  InputAdornment,
  FormControl,
  OutlinedInput,
  IconButton,
  Button,
} from "@material-ui/core";

import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

import { ReactComponent as GoogleLogo } from "../../assets/procurar.svg";
import PersonIcon from "@material-ui/icons/Person";
import LockIcon from "@material-ui/icons/Lock";

import firebase from "firebase/app";
import { auth } from "../../config/firebase";

const Login = ({ onPageChange }) => {
  console.log(auth.uid);
  const classes = useStyles();

  const [password, setPassword] = useState('');
  const [username, setUsername] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  };

  const handleSignIn = () => {
    if (username !== '' && password !== '')
      auth.signInWithEmailAndPassword(username, password);
  };

  return (
    <div>
      <FormControl fullWidth className={classes.margin} variant="outlined">
        <InputLabel htmlFor="outlined-adornment-amount">e-mail</InputLabel>
        <OutlinedInput
          id="outlined-adornment-amount"
          onChange={(event) => {
            setUsername(event.target.value);
          }}
          startAdornment={
            <InputAdornment position="start">
              <PersonIcon className={classes.icon} />
            </InputAdornment>
          }
          labelWidth={60}
        />
      </FormControl>
      <FormControl fullWidth className={classes.margin} variant="outlined">
        <InputLabel htmlFor="outlined-adornment-password">password</InputLabel>
        <OutlinedInput
          id="outlined-adornment-password"
          value={password}
          type={showPassword ? "text" : "password"}
          onChange={(e) => setPassword(e.target.value)}
          startAdornment={
            <InputAdornment position="start">
              <LockIcon className={classes.icon} />
            </InputAdornment>
          }
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword}
                onMouseDown={handleMouseDownPassword}
                edge="end"
              >
                {showPassword ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
          labelWidth={70}
        />
      </FormControl>
      <div className={classes.buttonWrapper}>
        <Button
          className={classes.buttonOutlined}
          style={{ marginRight: 7 }}
          variant="outlined"
          onClick={handleSignIn}
        >
          Entrar
        </Button>
        <Button
          className={classes.buttonOutlined}
          variant="outlined"
          onClick={() => onPageChange("SignUp")}
        >
          Cadastrar
        </Button>
      </div>
      <div className={classes.container}>
        <ButtonBase className={classes.buttonBase} onClick={signInWithGoogle}>
          <GoogleLogo className={classes.logoGoogle} />
          <Typography color="textSecondary">Entrar com o Google</Typography>
        </ButtonBase>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
  },
  logoGoogle: {
    width: "25px",
    height: "25px",
    padding: "0px 10px 5px 0px",
  },
  container: {
    textAlign: "center",
  },
  margin: {
    padding: "0px 0px 15px 0px",
  },
  icon: {
    fill: "#666666",
  },
  buttonOutlined: {
    flex: 1,
    fontWeight: "bold",
    color: "#666666",
    margin: "5px 0px 0px 0px",
  },
  buttonBase: {
    margin: "15px 0px 0px 0px",
  },
  buttonWrapper: {
    display: "flex",
  },
}));

export default Login;
